import React, { useEffect, useState } from 'react';
import axios from 'axios';

const Player = () => {
    useEffect(() => {
        play_audio();
    }, []);

    
    async function play_audio() {
        const response = await axios.get('/return_intro');

        let audio = new Audio(`/${response.data}.mp3`);
        audio.play();
    }

    return <div></div>;
}

export default Player;